@import url(https://fonts.googleapis.com/css?family=Dosis&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.AppContainer {
  background-color: black;
  background: url(/ncovid-2019-sgp-tracker/static/media/coronavirus-artwork.b71c98e4.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding-right: 20px 0px 20px 20px;
  background-position: 50% 0%;
  color: white;
  width: 100vw;
  height: 100%;

  font-family: "Dosis", sans-serif;
  font-size: 16px;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100vw;
  height: 100%;
}

.container {
  width: inherit;
  height: inherit;

  display: grid;
  grid-template-rows: 70px 1fr 100px;
  grid-template-columns: 1fr 250px;
  grid-column-gap: 20px;
  grid-template-areas: "header header"
                       "content right"
                       "footer footer";
  
  -webkit-animation: fadein 2s;
  
          animation: fadein 2s;
}

h2 {
  font-size: 2em;
  font-weight: bold;
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 1.5em;
  margin: 0;
  padding: 0;
}

.cases , .discharged, .treatment, .deaths {
  text-transform: uppercase;
}

.date {
  margin-bottom: 20px;
}

.cases {
  text-transform: uppercase;
  color: #b2dbd4;
}

.discharged {
  color: #42ff75;
}

.treatment {
  color: #ff3b3b;
}

.header {
  grid-area: header;
  text-align: right;
  align-self: center;
  padding: 20px;
}

.footer {
  grid-area: footer;
  padding: 20px;
  text-align: center;
}

.left {
  grid-area: left;
}

.right {
  grid-area: right;
  padding-right: 20px;
}

.sgp-map {
  -webkit-transform: translate(0, -25px);
          transform: translate(0, -25px);
}

/* tooltip */
.tooltip {
  background-color: #465c59;
  color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px;
  -webkit-animation: fadein 0.2s;
          animation: fadein 0.2s;
}

.circle:hover {
  fill: #465c59;
  stroke: #b2dbd4;
}

.tooltip_text {
  margin: 0;
  padding: 0;
  font-size: 14px;
}

/* nationality tally */
.nationalityTallyContainer {
  margin: 30px 0px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.tallyItem {
  display: flex;
  flex-direction: row;
  align-items: center;
}

/*age bar chart */
.ageBarChart {
  height: 200px;
  display: grid;
  grid-template-columns: 50px 1fr;
}

.ageBar {
  height: 18px;
  margin-bottom: 2px;
  background-color: #b2dbd4;
}

/* loading */
.loadingContainer {
  display: flex;
  width: inherit;
  height: inherit;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* date slider */
.dateSlider {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  width: 100%;
  height: 0.3em;
  border-radius: 0.3em;
  background-color: #b2dbd4;
}

.dateSlider::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  width: 0.5em;
  height: 2em;
  background: #b2dbd4;
  border: 0.2em solid #b2dbd4;
  /* border-radius: 1.2em; */
  cursor: pointer;
}

.dateSlider::-moz-range-thumb {
  -moz-appearance: none;
       appearance: none;
  width: 0.5em;
  height: 2em;
  background: #b2dbd4;
  border: 0.2em solid #b2dbd4;
  border-radius: 1.2em;
  cursor: pointer;
}

.dateSlider::-ms-thumb {
  appearance: none;
  width: 0.5em;
  height: 2em;
  background: #b2dbd4;
  border: 0.2em solid #b2dbd4;
  border-radius: 1.2em;
  cursor: pointer;
}

a:link {
  color: white;
}
